.footer-wrapper {
    background-color: rgb(41, 42, 56);
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    color: white;
    display: flex;
    width: 100%;
    gap: 20px;
    padding: 50px 0px;
}

.footerLinks-wrapper {
    display: flex;
    width: 80%;
    justify-content: space-between;
    align-items: center;
}

.f-link-sections {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.f-links-header {
    color: rgb(105, 105, 105);
}

hr {
    width: 80%;
    height: 1px;
    border: none;
    background-color: rgb(107, 107, 107);
}

.sub-footer {
    justify-content: space-between;
    align-items: center;
    width: 80%;

    display: flex;
}

@media screen and (max-width:900px) {
    .footerLinks-wrapper {
        display: flex;
        width: 80%;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }

    .sub-footer {
        justify-content: center;
        align-items: center;
        width: 90%;
        gap: 10px;
        display: flex;
        flex-wrap: wrap;
    }
}